import { Injectable } from '@angular/core';
import { TipoPermissaoEnum } from './tipo-permissao.enum';

@Injectable({ providedIn: 'root' })
export class PermissoesService {

  private permissoes: string;

  verificarAcesso(tipoPermissao: TipoPermissaoEnum) {
    this.pegarPermissoes()
    const permissao = this.permissoes.includes(tipoPermissao);
    return !!permissao;
  }

  pegarPermissoes() {
    const dados = JSON.parse(localStorage.getItem('dados-map'));
    this.permissoes = dados.permissoes ? dados.permissoes : '';
  }
}