import { AfterViewInit, Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { GoogleMap, MapInfoWindow, MapMarker } from "@angular/google-maps";
import { ActivatedRoute, Router } from "@angular/router";
import { BehaviorSubject } from "rxjs";
import { LoadModel, TipoLoadEnum } from "src/app/shared/components/load/load.model";
import { NotificacaoService } from "src/app/shared/components/notificacao/notificacao.service";
import { TabelaModel } from "src/app/shared/components/tabela/tabela.model";
import { AnalitycsService } from "../analitycs/analitycs.service";
import { CotacaoModel, EntregasModel } from "./cotacao.model";
import { CotacaoService } from "./cotacao.service";

@Component({
    selector: 'app-cotacao',
    templateUrl: './cotacao.component.html',
    styleUrls: ['./cotacao.component.scss']
})
export class CotacaoComponent implements OnInit {
    @ViewChild(GoogleMap, { static: false }) mapElement: GoogleMap;
    googleDirectionService = new google.maps.DirectionsService()
    maps: GoogleMap;
    map: any = {
        constrol: {},
        zoom: 12
    }
    options: any
    events: any[]
    value: number = 10;
    overlays: any[] = [];
    pointA: any;
    pointB: any;
    zoom: number = 7;

    valorCotacao: number;
    status: string = 'Proposta';
    cotacao: number

    polyneOptions: any;
    frota: any
    geocoder: any
    positions: any[] = []
    selectedValues: string
    loadConfig: LoadModel;
    form: FormGroup;
    item: CotacaoModel
    entregas: EntregasModel[]
    tpVeiculos: any[]
    selectedVeiculo: any
    submitButton: boolean = true;

    colunas: TabelaModel[]
    parametrosGeraisTabela: any = { first: 0, rows: 10 };

    title: string;

    constructor(
        private readonly route: ActivatedRoute,
        private readonly router: Router,
        private readonly analitycsService: AnalitycsService,
        private readonly cotacaoService: CotacaoService,
        private notificationService: NotificacaoService,
        private formBuilder: FormBuilder
        ) {
        this.geocoder = new google.maps.Geocoder()
    }

    async ngOnInit() {
        const status = this.route.snapshot.queryParamMap.get('status');
        if (status) {
            this.status = status
        }
        const id = this.route.snapshot.paramMap.get("id")
        const filial = this.route.snapshot.paramMap.get("filial")

        this.iniciaLoad(TipoLoadEnum.TELA_CHEIA, 'Carregando cotação...')
        await this.analitycsService
        .findAnalitycs()
        .then((itens: any) => {
            this.frota = itens.find(value => value.idCarga == id && value.filial == filial)
            this.item = {
                filial: this.frota.filial,
                idCotacao: this.frota.idCotacao,
            }
            this.title = `Rota: ${this.frota.filial}/${this.frota.idCotacao}`
            this.tpVeiculos = this.frota.tpVeiculo.split(', ')
            this.entregas = this.frota.entregas
            this.frota.dtEmbarque = `${this.frota.prevEmbarque.substring(6, 8)}/${this.frota.prevEmbarque.substring(4,6)}/${this.frota.prevEmbarque.substring(0,4)}`
            this.centralizaMapa()
            this.populaEventos()
            this.populaColuna()
            this.fechaLoad()
        })
        .catch(error => {
            console.error(error)
            this.fechaLoad()
            this.notificationService.addNotification('error', 'Erro!', 'Não foi possível carrega as informações.')
        })

        if (this.frota.proposta.emiteCte) {
            if (this.status != 'Negociacao') {
                this.status = this.frota.proposta.status ? this.frota.proposta.status : 'Proposta';
            }
            this.selectedValues = this.frota.proposta.emiteCte;
            this.valorCotacao = this.frota.proposta.valorProposta;
            this.selectedVeiculo = this.tpVeiculos[this.tpVeiculos.indexOf(this.frota.proposta.tpVeiculo)]
        }

        setTimeout(() => {
            this.populaMapa()
        }, 4000)

        this.criarFormulario(this.item ? this.item : new CotacaoModel())
    }

    private populaEventos() {
        let eventos = [{
            status: this.frota.localInicio
        }];
        for (let i = 0; i < this.entregas.length; i++) {
            const element = this.entregas[i];
            eventos.push({ status: element.localEntrega })
            this.buscaLocalizacao(element.localEntrega, 'entrega', element.seq, element)
        }
        this.events = eventos;
    }

    private centralizaMapa() {
        this.buscaLocalizacao(this.frota.localInicio, 'localInicio', 0)
        this.buscaLocalizacao(this.frota.localTermino, 'localTermino', this.frota.entregas.length + 1)
    }

    private populaColuna() {
        this.colunas = [
            { header: 'Id', field: 'seq', align: 'left', noSort: true },
            { header: 'Local de Entrega', field: 'localEntrega', align: 'left', noSort: true },
            { header: 'Peso', field: 'peso', align: 'left', noSort: true }
        ]
    }

    private criarFormulario(cotacao?: CotacaoModel) {
        this.form = this.formBuilder.group({
            filial: [cotacao.filial],
            idCotacao: [cotacao.idCotacao],
            emiteCte: [cotacao.emiteCte, Validators.required],
            status: [this.status],
            tpVeiculo: [cotacao.tpVeiculo, Validators.required],
            valorProposta: [cotacao.valorProposta, Validators.required],
            observacao: [cotacao.observacao]
        })
    }

    private buscaLocalizacao(address, type, seq, entrega?: EntregasModel) {
        this.iniciaLoad(TipoLoadEnum.TELA_CHEIA, 'Carregando localização...')
        this.geocoder.geocode({
                address
            }, async (results, status) => {
                if (status == google.maps.GeocoderStatus.OK) {
                    const lat = results[0].geometry.location.lat()
                    const lng = results[0].geometry.location.lng()
                    const position = new google.maps.LatLng(lat, lng)
                    this.positions.push({
                        location: position,
                        stopover: true
                    })
                    if (type == 'localInicio') {
                        this.pointA = position
                    }
                    if (type == 'localTermino') {
                        this.pointB = position
                    }
                    await this.addMarkers(position, seq, entrega ? entrega : {
                        localEntrega: address,
                        peso: 0,
                        seq,
                        comecoOuFimDeRota: true
                    })
                    this.fechaLoad()
                    return;
                }
            });

    }

    private async addMarkers(position, seq, entrega?: EntregasModel) {
        // if (!entrega.comecoOuFimDeRota) {
            const marker = {
                position,
                label: {
                    color: '#000000',
                    text: `${entrega.localEntrega}`
                },
                options: {
                    animation: google.maps.Animation.BOUNCE
                },
                title: `Entrega: ${entrega.seq} \nPeso: ${entrega.peso} \nLocal Entrega: ${entrega.localEntrega}`
            }
            this.overlays.push(marker)
        // } else {
        //     const marker = {
        //         position,
        //         label: {
        //             color: '#ff5d4f',
        //             text: `${entrega.localEntrega}`
        //         },
        //         options: {
        //             animation: google.maps.Animation.BOUNCE
        //         },
        //         title: `Entrega: ${entrega.seq} \nPeso: ${entrega.peso} \nLocal Entrega: ${entrega.localEntrega}`
        //     }
        //     this.overlays.push(marker)
        // }

    }

    private async populaMapa() {
        this.iniciaLoad(TipoLoadEnum.TELA_CHEIA, 'Carregando mapa...')
        this.googleDirectionService.route({
            origin: this.pointA,
            destination: this.pointB,
            waypoints: this.positions,
            optimizeWaypoints: true,
            travelMode: google.maps.TravelMode.DRIVING,

        }, (results, status) => {
            if (google.maps.DirectionsStatus.OK == status){
                const routes = results.routes[0].overview_path;
                const points = [];
                for (let i = 0; i < routes.length; i++) {
                    points[i] = new google.maps.LatLng(routes[i].lat(), routes[i].lng())
                }
                this.polyneOptions = {
                    path: [...points ],
                    strokeColor: '#ff5d4f',
                    strokeOpacity: 1.0,
                    strokeWeight: 2,
                }
                this.fechaLoad()
            } else {
                this.fechaLoad()
                this.notificationService.addNotification('error', 'Erro!', 'Não foi possível carrega as informações.')
            }

        })

    }

    async enviarCotacao(): Promise<void> {
        this.iniciaLoad('Enviando cotação...')
        this.submitButton = false;
        const veiculo = this.form.value.tpVeiculo;
        if (veiculo.indexOf(' ') == 0) {
            this.form.value.tpVeiculo = veiculo.replace(/^./, "")
        }

        this.cotacaoService
        .enviarCotacao(this.form.value)
        .then(itens => {
            this.fechaLoad()
            this.notificationService.addNotification('success', 'Sucesso!', 'Cotação enviada com sucesso!')
            setTimeout(() => {
                this.router.navigate(['home'])
            }, 3000)
        })
        .catch(error => {
            this.submitButton = true;
            this.fechaLoad()
            this.notificationService.addNotification('error', 'Erro!', 'Ocorreu um erro inesperado, tente novamente!')
        })
    }


    iniciaLoad(tipo, message?){
        this.loadConfig = {tipo, mensagem: message ? message : '' }
    }

    fechaLoad(){
        this.loadConfig = null;
    }

    public infoOpen(marker: MapMarker, infoWindow: MapInfoWindow) {
       infoWindow.open(marker);
    }
}
