import { Injectable } from '@angular/core';
import { PermissoesService } from 'src/app/core/permissoes/permissoes.service';
import { ValidadorModel } from '../models/validadores.model';

@Injectable({providedIn: 'root'})
export abstract class CondicaoService {

    constructor(public permissoesService: PermissoesService,) { }

    public avaliaCondicao (itemAvaliado:any, validador:ValidadorModel, condicao:any){
      // Compara se contem um item 
      if(validador === ValidadorModel.INDEX_OFF && itemAvaliado){
        const item = itemAvaliado.toString();
        return item && condicao.indexOf(item) != -1;
      }
       // Compara se nao contiver um item
      if(validador === ValidadorModel.NO_INDEX_OFF && itemAvaliado){
        const item = itemAvaliado.toString();
        return item && condicao.indexOf(item) == -1;
      }
      // Avalia a condição quando um array e maior do que o a condição proposta
      if(validador === ValidadorModel.ITEM_LENGTH_MAIOR_QUE && itemAvaliado){
        return itemAvaliado.length > condicao;
      }
      // Avalia a condição diferente
      if(validador === ValidadorModel.DIFERENTE && itemAvaliado != condicao){
        return true
      }
         // Avalia a condição igual
      if(validador === ValidadorModel.IGUAL && itemAvaliado == condicao){
        return true
      }
      // Avalia se existe uma valor
      if(validador === ValidadorModel.ATRIBUTO_EXISTENTE  && itemAvaliado){
        return true
      }
      // Avalia se item vazio
      if(validador === ValidadorModel.ATRIBUTO_NAO_EXISTENTE && !itemAvaliado){
        return true
      }
      // Verifica Pemissao de acesso
      if(validador === ValidadorModel.PERMISSAO_DE_ACESSO && condicao){
        const permitido = this.permissoesService.verificarAcesso(condicao)
        return permitido
      }
      // Verifica se o valor e verdadeiro
      if(validador === ValidadorModel.PARAMETRO_CONDICAO_TRUE && itemAvaliado === condicao){
        return true
      }
      // Verifica se nao existe coluna para avaliar e se a condicao e verdadeira (Caso dos botoes de salvar e editar)
      if(validador === ValidadorModel.PARAMETRO_CONDICAO_TRUE && !itemAvaliado && condicao){
        return true
      }
      return false
  };

}
