import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { LoadModel, TipoLoadEnum } from "src/app/shared/components/load/load.model";
import { NotificacaoService } from "src/app/shared/components/notificacao/notificacao.service";
import { ActionEmitTableModel, TabelaModel } from "src/app/shared/components/tabela/tabela.model";
import { BotoesCommand, BotoesTabeleClass } from "src/app/shared/models/botoes.model";
import { IconsEnum } from "src/app/shared/models/icons-enum";
import { AnalitycsModel } from "./analitycs.model";
import { AnalitycsService } from "./analitycs.service";

@Component({
    selector: 'app-analitycs',
    templateUrl: './analitycs.component.html',
    styleUrls: ['./analitycs.component.scss']
})
export class AnalitycsComponent implements OnInit {

    colunas: TabelaModel[];
    itenstabela: AnalitycsModel[];
    parametrosGeraisTabela: any = {first: 0, rows: 10};
    loadConfig: LoadModel;

    constructor(
        private readonly analitycsService: AnalitycsService,
        private readonly router: Router,
        private notificationService: NotificacaoService
        ) {}

    ngOnInit(): void {
        this.iniciaLoad('Carregando tabela...')
        this.populaColuna()
        this.populaItens()
    }

    private populaColuna() {
        this.colunas = [
            { header: 'Rota', field: 'filialConcatenada', align: 'left', noSort:true },
            { header: 'Previsão de Início', field: 'dtEmbarque', align: 'left', noSort:true },
            { header: 'Veículo preferencial', field: 'tpVeiculo', align: 'left', noSort:true },
            { header: 'Entregas', field: 'qtdEntregas', align: 'left', noSort:true  },
            { header: 'Distancia "Km"', field: 'distancia', align: 'left', noSort:true  },
            { header: 'Local de Inicio', field: 'localInicio', align: 'left', noSort:true  },
            { header: 'Local Termino', field: 'localTermino', align: 'left', noSort:true  },
            { header: 'Ação', field: 'acao', noSort: true, align: 'center', botoes: [
                { title: 'Enviar Cotação', icon: IconsEnum.DINHEIRO, baotaoClass: BotoesTabeleClass.SUCCESS, command: BotoesCommand.EDITAR }
            ] }
        ]
    }

    private async populaItens() {
        await this.analitycsService
        .findAnalitycs()
        .then(itens => {
            itens.map(value => {
                value.filialConcatenada = `${value.filial}/${value.idCotacao}`
                value.qtdEntregas = value.entregas.length
                value.dtEmbarque = `${value.prevEmbarque.substring(6, 8)}/${value.prevEmbarque.substring(4,6)}/${value.prevEmbarque.substring(0,4)}`
            })
            this.itenstabela = itens;
            this.fechaLoad()
        })
        .catch(error => {
            this.fechaLoad()
            this.notificationService.addNotification('error', 'Erro!', error)
        })
    }

    acoesTabela(event:ActionEmitTableModel) {
        this.router.navigate([`home/cotacao/${event.item.idCarga}/filial/${event.item.filial}`], {
            state: {
                item: JSON.stringify(event.item)
            }
        })
    }

    iniciaLoad(message?){
        this.loadConfig = {tipo: TipoLoadEnum.TELA_CHEIA, mensagem: message ? message : '' }
    }

    fechaLoad(){
        this.loadConfig = null;
    }
}
