import { ErrorHandler, Injectable } from "@angular/core";
import axios, { AxiosInstance } from "axios";
import { environment } from "src/environments/environment";
import { AnalitycsModel } from "./analitycs.model";

@Injectable()
export class AnalitycsService {

    protected url = environment.apiUrl
    private axiosClient: AxiosInstance
    private erroHandler: ErrorHandler
    private userId: string;

    constructor() {
        this.userId = localStorage.getItem('userId')
        this.axiosClient = axios.create({
            timeout: 3000,
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*',
                'Authorization': `Bearer ${localStorage.getItem('token')}`
            },
            baseURL: this.url
        })
    }

    public async findAnalitycs(): Promise<AnalitycsModel[]> {
        try {
            let axiosResponse = await this.axiosClient.get(`/tivws100/cotacaofrete?userId=${this.userId}`)
            return axiosResponse.data;
        } catch (error) {
            return error
        }
    }

}
