import { ErrorHandler, Injectable } from "@angular/core";
import axios, { AxiosInstance } from "axios";
import { environment } from "src/environments/environment";
import { CotacaoModel } from "./cotacao.model";

@Injectable()
export class CotacaoService {
    protected url = environment.apiUrl
    private axiosClient: AxiosInstance
    private erroHandler: ErrorHandler
    private userId: string;

    constructor() {
        this.userId = localStorage.getItem('userId');
        this.axiosClient = axios.create({
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*',
                'Authorization': `Bearer ${localStorage.getItem('token')}`
            },
            baseURL: this.url
        })
    }

    public async enviarCotacao(entidade: CotacaoModel) {
        // try {
            return await this.axiosClient.post(`/tivws100/cotacaofrete?userId=${this.userId}`, entidade);
        // } catch (error) {
        //     console.error(error)
        //     return error;
        // }
    }
}

