import { HttpParams } from "@angular/common/http";
import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { LazyLoadEvent } from "primeng/api";
import { finalize } from "rxjs/operators";
import { NotificacaoService } from "../notificacao/notificacao.service";
import {
    ActionEmitTableModel,
    ParametroTabela,
    TabelaModel,
} from "./tabela.model";
import { TabelaService } from "./tabela.service";

@Component({
    selector: "app-tabela",
    templateUrl: "./tabela.component.html",
    styleUrls: ["./tabela.component.scss"],
})
export class TabelaComponent implements OnInit {
    @Output() eventAcaoBotao = new EventEmitter();
    @Output() eventListaResuldo = new EventEmitter();

    @Output() noRegisters = new EventEmitter();
    @Input() url: string;
    @Input() cols: TabelaModel[];
    @Input() loadOnInit = true;
    @Input() ordenacaoPaginacaoExterna = false;
    @Input() paginacao = false;
    @Input() parametros: any;
    @Input() subTotal = false;
    @Input() quantidadeItens = false;
    @Input() itens: any[] = [];
    @Input() totalRegistros;
    @Input() noTruncat;
    @Input() estiloPadraoMap3c = false;
    @Input() parametrosTabela: ParametroTabela = {
        first: 0,
        rows: 10,
        sortField: null,
        sortOrder: null,
    };
    @Input() rowsPerPageOptions: any = [5, 10, 20, 30, 50, 100, 150];
    loading = false;

    constructor(
        private tableService: TabelaService,
        private notificacaoService: NotificacaoService
    ) {}

    async ngOnInit(): Promise<void> {
        this.tableService.setUrl(this.url);

        if (this.loadOnInit) {
            this.pesquisaComHistorico();
        }
    }

    async pesquisaComHistorico() {
        const historico = await this.resgataHistorico();
        // this.pesquisa(historico ? historico : this.parametros);
        this.pesquisa(this.parametros);
    }

    async pesquisa(param?: any) {
        let parametros: any = param ? param : {};
        let httpParams = new HttpParams();
        parametros.page = this.parametrosTabela.first;
        parametros.limit = this.parametrosTabela.rows;
        if (this.parametrosTabela.sortOrder) {
            parametros.sortDirection =
                this.parametrosTabela.sortOrder === 1 ? "DESC" : "ASC";
        }
        if (this.parametrosTabela.sortField) {
            parametros.sortColumn = this.parametrosTabela.sortField;
        }
        this.salvaHistorico(parametros);

        // Monta parametros de pesquisa caso tenha recebido algum
        const kysFormulario = Object.keys(parametros);
        for (const elemento of kysFormulario) {
            if (parametros[elemento]) {
                httpParams = httpParams.set(elemento, parametros[elemento]);
            }
        }

        this.loading = true;
        this.tableService
            .findAll(httpParams)
            .pipe(finalize(() => (this.loading = false)))
            .subscribe(
                (resposta: any) => {
                    if (resposta.body) {
                        this.itens = resposta.body.dados;
                        this.totalRegistros = resposta.body.total;
                    }
                    this.eventListaResuldo.emit(resposta.body);
                },
                (error) => {
                    this.itens = [];
                    this.totalRegistros = 0;
                    this.eventListaResuldo.emit([]);
                    this.notificacaoService.addNotification(
                        "error",
                        "Erro!",
                        "Erro ao localizar itens!"
                    );
                }
            );
    }

    salvaHistorico(parametros) {
        const historico = { tela: this.url, filtro: parametros };
        window.sessionStorage.setItem(
            "filtroHistorico",
            JSON.stringify(historico)
        );
    }

    async resgataHistorico() {
        let historico: any = await window.sessionStorage.getItem(
            "filtroHistorico"
        );
        historico = JSON.parse(historico);
        historico =
            historico && historico.tela == this.url ? historico.filtro : null;
        // Seta os parametros basicos da tabela se tiver historico
        if (historico) {
            // this.parametrosTabela.first = historico.page ? historico.page : this.parametrosTabela.first;
            this.parametrosTabela.rows = historico.limit
                ? historico.limit
                : this.parametrosTabela.rows;
            const sort: any =
                historico.sortDirection && historico.sortDirection == "DESC"
                    ? 1
                    : 0;
            this.parametrosTabela.sortOrder = sort
                ? sort
                : this.parametrosTabela.sortOrder;
            this.parametrosTabela.sortField = historico.sortColumn
                ? historico.sortColumn
                : this.parametrosTabela.sortField;
        }
        this.parametros = historico;
        return historico;
    }

    chamadaDuplicada = true;
    async loadCarsLazy(event: LazyLoadEvent) {
        const columnFound:TabelaModel = this.cols.find(
            (column) => column.field === event.sortField
        );
        if (
            (this.loadOnInit && !columnFound) ||
            (!columnFound?.noSort && this.chamadaDuplicada)
        ) {
            await this.resgataHistorico();
            this.parametrosTabela = event;
            this.pesquisa(this.parametros);
            this.chamadaDuplicada = false;
        }
        // Este metodo existe porque o componete esta mandando varias chamado a cada click
        setTimeout(() => {
            this.chamadaDuplicada = true;
        }, 100);
    }

    acaoBotao(event: ActionEmitTableModel, item) {
        event.item = item;
        this.eventAcaoBotao.emit(event);
    }

    calculoSubtotais(coluna) {
        let total = 0;
        for (const iterator of this.itens) {
            total += iterator[coluna.field]
                ? parseFloat(iterator[coluna.field])
                : 0;
        }
        coluna.total = total - total == 0 ? total : "--";
        return total - total == 0 ? total : "--";
    }

    verificaTruncat(coluna) {
        if (coluna.header && coluna.field) {
            let item = document.getElementById(coluna.field);
            if (item.offsetWidth < item.scrollWidth) {
                coluna.truncat = true;
                return;
            }
        }
        coluna.truncat = false;
    }
}
