import {Component, OnInit, Input, EventEmitter, Output} from '@angular/core';
import { BotaoTabela } from 'src/app/shared/models/botoes.model';
import { CondicaoService } from 'src/app/shared/services/condicao-service';

@Component({
  selector: 'app-buttons',
  templateUrl: './buttons.component.html',
  styleUrls: ['./buttons.component.scss']
})
export class ButtonsComponent implements OnInit {

  @Output() actionButton = new EventEmitter();
  @Input() botoes;
  @Input() elemento;
  @Input() coluna;
  @Input() edit = false;
  @Input() estiloPadraoMap3c = false;

  constructor(
    private condicaoService: CondicaoService,
  ) {}

  ngOnInit() {
  }

  acaoBotao(action) {
    let event: any = {};
    event.action = action;
    this.actionButton.emit(event);
  }

  validaBotao(botao:BotaoTabela){
    if(botao?.condition?.length > 0){
      let resultado = true;
      for (const element of botao.condition) {
        const avaliacao = this.condicaoService.avaliaCondicao(
          this.elemento[element.colunaBase], element.validator, element.condicao       
        )
        if(!avaliacao){
          resultado = avaliacao;
        }
      }
      return resultado
    } 
    else {
      return true;
    }
  }

}
